.pricing-component {
  padding: 20px;

  .header {
    margin-bottom: 28px;
    
    h2{
      // border: 2px solid red;
    }
  }
}

.toggle {
  .annually {
    display: inline-block;
  }

  .slider {
    background: linear-gradient(
      to right,
      hsl(236, 72%, 79%),
      hsl(237, 63%, 64%)
    );
    border-radius: 34px;
    cursor: pointer;
    display: inline-block;
    height: 34px;
    position: relative;
    top: 12px;
    margin-left: 12px;
    margin-right: 12px;
    outline: none;
    width: 60px;
    
    &:hover {
      background: linear-gradient(
        to right,
        hsla(236, 72%, 79%, 0.6),
        hsl(237, 63%, 64%, .6)
        );
        outline: none;
        &:active{
          background: linear-gradient(
            to right,
            hsla(236, 72%, 79%, 0.6),
            hsl(237, 63%, 64%, .6)
            );
            outline: none;
          }
          &:focus{
      background: linear-gradient(
        to right,
        hsla(236, 72%, 79%, 0.6),
        hsl(237, 63%, 64%, .6)
        );
        outline: none;
      }
    }
    
    
    .slider-button {
      background-color: white;
      border-radius: 70%;
      position: absolute;
      left: 5px;
      top: 4px;
      height: 77%;
      outline: none;
      width: 44%;
    }
  }

  .monthly {
    display: inline-block;
  }
}

.left-action {
  transform: translateX(0px);
  transition: 0.2s;
}
.right-action {
  transform: translateX(24px);
  transition: 0.2s;
}
